.backdrop {
  background-color: #0000001a;
  position: fixed;
  inset: 0;
}

.popup {
  opacity: 1;
  background-color: #fff;
  border-radius: 5px;
  min-width: 310px;
  max-width: 450px;
  height: 465px;
  padding: 200px 30px 0;
  transition-property: transform, opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is__hidden .popup {
  opacity: 0;
  transform: translate(-50%, -200%)scale(.1);
}

.close__button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #0000001a;
  border-radius: 100px;
  padding: 6px 9px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close__button:hover {
  color: var(--light-blue);
}

.backdrop.is__hidden {
  opacity: 0;
  pointer-events: none;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.no__scrool {
  overflow: hidden;
}

